* {
  margin: 0;
  padding: 0;
  outline: 0;
}

body,
html,
#root,
.App,
#Preview,
#Events {
  overflow-y: hidden !important;
  overflow-x: hidden !important;
}
#template-container,
#event-container {
  overflow-y: scroll !important;
  overflow-x: hidden !important;
}

.App {
  font-family: Open Sans, sans-serif;
  color: #212529;
}



.main-header {
  width: 100vw;
  height: 10vh;
  background-color: #d22820;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
  box-shadow: 0px 1px 10px #999;
  margin-bottom: 10px;
}

nav.navbar {
  background-color: #d22820;
  box-shadow: 0px 1px 10px #999;
  margin-bottom: 10px;
}

.navbar-brand img[alt="Fightback Logo"] {
  height: 10vh;
  max-height: 5rem;
}

.main-header img,
.main-header div {
  padding: 5px;
  width: 20vw;
  text-align: center;
  font-size: calc(10px + 3vmin);
}

 .button.is-danger.is-inverted {
color:#d22820!important;
}

 .button.is-danger.is-outlined, .button.is-danger.is-outlined, .button.is-danger.is-outlined, 
.button.is-danger.is-outlined {
  border-color:#d22820!important;
  color:#d22820!important;
}


 .button.is-danger.is-outlined.is-focused, 
 .button.is-danger.is-outlined.is-hovered, 
 .button.is-danger.is-outlined:focus, 
 .button.is-danger.is-outlined:hover {
  background-color: #d22820!important;
  border-color:#d22820!important;
  color:#fff!important;
}

#content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  height: calc(90vh - 10px);
}

#delete-notification {
  position: absolute;
  bottom: 80px;
  right: 10px;
}

#success-notification {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.sectionNav {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

/* Events Section */
#Events {
  flex: 3 1 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: scroll;
}

#Events > nav > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: white;
}
#Events > nav > div input {
  margin-right: 10px;
}
#Events > nav > h1 {
  display: inline;
  margin-bottom: 0;
}
#Events > nav > button {
  width: 10%;
}

#add-button {
  margin-right: 15px;
}

#event-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
}

.EventCard {
  margin: 5px;
  width: 95%;
}

.TitleEditorCard{
  width: 25vw;
  padding: 0.75rem 0.5rem;
  border: 1px solid #d22820;
}

.input,
.textarea {
  font-size: 1rem;
  padding-bottom: 0 !important;
  padding-top: 0 !important;
}

.input {
  height: 2em !important;
}

.textarea {
  height: 7em !important;
  min-height: 7em !important;

}

/* Preview Section */

#Preview {
  flex: 2 1 0;
  height: 100%;
  border-left: #212529 solid 1px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: scroll;
}

#template-container {
  width: 50vh;
  height: 80vh;
  border: #212529 solid 1px;
  overflow: scroll;
}

.box.EventCard > header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.box.EventCard > header > h1 {
  margin-top: 0.5rem;
}
